.tippy-box[data-theme=cfpb] {
  background-color: var(--gray-5);
  border: 1px solid var(--gray-40);
  border-radius: 0;
  color: var(--black);
  padding: 0.9375rem;
}
.tippy-box[data-theme=cfpb] .tippy-arrow {
  color: var(--gray-5);
}
.tippy-box[data-theme=cfpb] .tippy-heading {
  font-weight: 500;
  font-size: 1.125rem;
}
.tippy-box[data-theme=cfpb] .tippy-body {
  font-size: 1rem;
  margin-top: 0.9375rem;
}